.custom-checkbox {
  margin-bottom: 30px;
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: red solid 2px;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

.custom-checkbox__text {
  position: relative;
  padding-left: 30px;
  font-weight: 400;
  font-size: 15px;
  line-height: 130%;
  color: #2d2c3d;
}
@media (max-width: 650px) {
  .custom-checkbox__text {
    font-size: 11px;
  }
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid #949494;
  border-radius: 3px;
  width: 18px;
  height: 18px;
}

.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  background: url("../img/check.svg");
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s;
}

.custom-checkbox a {
  color: inherit;
}

.custom-checkbox a:hover {
  color: var(--accent-color);
}

.custom-checkbox a:active {
  color: var(--white-color);
}

.custom-checkbox input {
  appearance: none;
}

.custom-checkbox input:focus + .custom-checkbox__text::before {
  outline: 2px dashed var(--white-color);
  outline-offset: 3px;
}

.custom-checkbox input:checked + .custom-checkbox__text::after {
  opacity: 1;
}

.custom-checkbox a.focus-visible {
  outline: 2px dashed var(--white-color);
  outline-offset: 3px;
}

