.discover {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__content {}

  &__list {}

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 13px;
    }

    &:first-child {
      gap: 200px;

      @include small-tablet {
        gap: 95px;
      }

      .discover__svg {
        @include desktop {
          height: 90px;
        }

        @include tablet {
          height: 80px;
        }

        @include small-tablet {
          height: 70px;
        }

        @include mobile {
          height: 80px;
        }
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -100px;
        }
      }
    }

    &:nth-child(2) {
      gap: 175px;

      @include small-tablet {
        gap: 90px;
      }

      .discover__svg {
        @include desktop {
          height: 84px;
        }

        @include tablet {
          height: 73px;
        }

        @include small-tablet {
          height: 65px;
        }

        @include mobile {
          height: 80px;
        }
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -100px;
        }
      }
    }

    &:nth-child(3) {
      // height: 110px;
      gap: 150px;

      @include small-tablet {
        gap: 85px;
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -100px;
        }
      }
    }

    &:nth-child(4) {
      // height: 78px;
      gap: 125px;

      @include small-tablet {
        gap: 80px;
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -110px;
        }
      }
    }

    &:nth-child(5) {
      // height: 78px;
      gap: 100px;

      @include small-tablet {
        gap: 75px;
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -120px;
        }
      }
    }

    &:nth-child(6) {
      // height: 78px;

      gap: 75px;

      @include small-tablet {
        gap: 70px;
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -130px;
        }
      }
    }

    &:nth-child(7) {
      // height: 78px;
      gap: 50px;

      @include small-tablet {
        gap: 65px;
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -145px;
        }
      }
    }

    &:last-child {
      font-weight: 600;
      gap: 25px;

      svg {
        @include desktop {
          height: 70px;
        }

        @include tablet {
          height: 60px;
        }

        @include small-tablet {
          height: 55px;
        }

        @include mobile {
          height: 100%;
        }
      }

      .discover__svg {
        @include mobile {
          margin-right: auto;
          margin-left: auto;
          width: 160px;
          height: 65px;
        }
      }

      .discover__item-inner::before {
        left: -175px;

        @include small-tablet {
          left: -130px;
        }
      }
    }
  }

  &__svg {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 715px;

    @include desktop {
      height: 80px;
      max-width: 526px;
    }

    @include tablet {
      height: 67px;
      max-width: 460px;
    }

    @include small-tablet {
      height: 60px;
      max-width: 400px;
    }

    @include mobile {
      overflow: hidden;
      height: 80px;
    }

    svg {
      opacity: .8;

      @include desktop {
        width: 100%;
        height: 100%;
      }

      @include mobile {
        position: absolute;
        z-index: -1;
        width: 500px;
      }
    }
  }

  &__num {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    font-weight: 400;
    font-size: 70px;
    line-height: 110%;
    text-align: center;
    color: #fff;
    translate: -50% -50%;

    @include desktop {
      font-size: 46px;
    }

    @include tablet {
      font-size: 38px;
    }

    @include mobile {
      display: none;
    }
  }

  &__num-text {
    position: absolute;
    left: 50%;
    top: 50%;
    display: none;
    font-weight: 400;
    font-size: 13px;
    line-height: 110%;
    text-align: center;
    color: #fff;
    translate: -50% -50%;

    @include mobile {
      display: inline-block;
    }
  }

  &__item-inner {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 120px;
      height: 2px;
      background: linear-gradient(#fff 0%, #084BC9 100%);

      @include small-tablet {
        width: 80px;
      }

      @include mobile {
        display: none;
      }
    }

    @include mobile {
      display: none;
    }
  }
}
