.breadcrumbs {
  margin-top: 30px;
  margin-bottom: 30px;

  @include mobile {
    margin-top: 20px;
    margin-bottom: 20px;

  }
}

.breadcrumbs__list {
  display: inline-flex;

  // flex-wrap: wrap;
}

.breadcrumbs__item {
  position: relative;
  display: inline;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 14px;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  position: absolute;
  right: -10px;
  top: 4px;
  color: #0c5eb8;

  @include mobile {
    top: 2px;
  }
}

.breadcrumbs__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 98%;
  color: #2d2c3d;
  transition: color 0.3s ease-in-out;

  @include small-tablet {
    font-size: 15px;
  }

  @include mobile {
    font-size: 13px;
  }
}

.breadcrumbs__link:not([href]) {
  font-weight: 600;
  text-decoration-line: inherit;
  color: #084bc9;
}
