.approach {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__wrapper {
    position: relative;
    padding-bottom: 180px;

    @include desktop {
      padding-bottom: 90px;
    }

    @include small-tablet {
      padding-bottom: 80px;
    }

    @include mobile {
      padding-bottom: 60px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 90px 30px;

    @include desktop {
      gap: 20px;
    }
  }

  &__card {
    position: relative;

    @include mobile {
      margin-bottom: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 5px;
      top: -180px;
      width: 2px;
      height: 150px;
      background: linear-gradient(#fff 0%, #084BC9 100%);
      opacity: 0; /* Начальное состояние */

      @include mobile {
        display: none;
      }
    }

    /* Анимация keyframes */
    @keyframes fade {
      0% {
        opacity: 0; /* Начальное состояние */
      }

      100% {
        opacity: 1; /* Конечное состояние */
      }
    }

    /* Применение анимации при активации AOS */
    &[data-aos].aos-animate {
      &::before {
        opacity: 1; /* Убедитесь, что элемент видим */
        animation: fade 10s forwards; /* Запуск анимации при прокрутке */
      }
    }

    &:nth-child(3n+2) {
      margin-top: 100px;

      @include small-tablet {
        margin-top: 70px;
      }

      @include mobile {
        margin-top: 0;
      }
    }

    &:nth-child(3n+3) {
      margin-top: 200px;

      @include small-tablet {
        margin-top: 150px;
      }

      @include mobile {
        margin-top: 0;
      }
    }
  }

  &__card-title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 38px;
    line-height: 110%;

    @include tablet {
      margin-bottom: 10px;
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__card-text {
    margin: 0;
    padding-bottom: 50px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      padding-bottom: 20px;
      font-size: 15px;
    }

    @include mobile {
      padding-bottom: 0;
    }
  }
}

.approach-slider {
  overflow: visible;

  .approach-slider__pag {
    display: none;

    @include mobile {
      display: block;
    }
  }

  @include mobile {
    overflow: hidden;
    padding-bottom: 20px;

    .swiper-wrapper {
      display: flex;
      column-gap: 0;
    }
  }
}

.selection .approach__wrapper {
  padding-bottom: 0;
}
