.growth {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 48px;
    line-height: 110%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 34px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__title-gradient {
    -webkit-text-fill-color: transparent;
    background: linear-gradient(181deg, #da0202 40%, #084bc9 100%);
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__list {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;

    // .growth__dot {
    //   &:nth-child(odd) {
    //     padding-left: 42px;

    //     &::before {
    //       left: 0;
    //     }
    //   }

    //   &:nth-child(even) {
    //     padding-right: 42px;

    //     &::before {
    //       right: 0;
    //     }
    //   }
    // }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    width: calc(50% + 10px);

    @include mobile {
      width: 100%;
    }

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      width: 4px;
      height: calc(100% + 30px);
      background-image: url('../img/line.svg');
      background-repeat: no-repeat;
      opacity: 0.7;

      @include mobile {
        display: none;
      }
    }

    &:first-child::after {
      top: 50%;
    }

    &:last-child::after {
      top: inherit;
      bottom: 50%;
    }

    &:nth-child(even) {
      flex-direction: row-reverse;

      // padding-right: 42px;
      text-align: right;

      .growth__dot {
        padding-right: 42px;

        &::before {
          right: 0;
        }
      }

      @include mobile {
        flex-direction: row;
        padding-right: 0;
        text-align: left;
      }

      &::before {
        right: 0;
      }

      &::after {
        right: 8px;
      }

      .growth__text {
        padding-right: 20px;

        @include mobile {
          padding-right: 0;
          padding-left: 20px;
        }
      }
    }

    &:nth-child(odd) {
      justify-self: flex-end;
      margin-left: auto;

      // padding-left: 42px;

      .growth__dot {
        padding-right: 42px;

        &::before {
          left: 0;
        }
      }

      @include mobile {
        justify-self: start;
        margin-left: 0;
        padding-left: 0;
      }

      &::before {
        left: 0;
      }

      &::after {
        left: 8px;
      }

      .growth__text {
        padding-left: 20px;
      }
    }

    &:nth-child(5) svg {
      fill: transparent;
      stroke: url("#paint1_linear_479_2043");
      stroke-width: 1.5;
    }
  }

  &__svg {
    flex-shrink: 0;
    width: 70px;
    height: 70px;
    fill: url("#paint1_linear_479_2043");

    @include desktop {
      width: 50px;
      height: 50px;
    }

    @include tablet {
      width: 40px;
      height: 40px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 28px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 13px;
    }
  }

  &__dot {
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      background-color: #2d2c3d;
      translate: 0 -50%;

      @include mobile {
        display: none;
      }
    }
  }
}
