.candidate {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    @include tablet {
      padding-right: 60px;
      padding-left: 60px;
    }

    @include small-tablet {
      padding-right: 20px;
      padding-left: 20px;
    }

    @include mobile {
      padding-right: 0;
      padding-left: 0;
    }
  }

  &__list {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 42px;

    @include mobile {
      padding-left: 0;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      background-color: #2d2c3d;
      translate: 0 -50%;

      @include mobile {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 8px;
      top: 0;
      width: 4px;
      height: calc(100% + 30px);
      background-image: url('../img/line.svg');
      background-repeat: no-repeat;
      opacity: 0.7;

      @include mobile {
        display: none;
      }
    }

    .candidate__text {
      padding-left: 20px;
    }

    &:not(:last-child) {
      margin-bottom: 30px;

      @include tablet {
        margin-bottom: 20px;
      }

      @include mobile {
        margin-bottom: 10px;
      }
    }

    &:first-child::after {
      top: 50%;
    }

    &:last-child::after {
      top: inherit;
      bottom: 50%;
    }
  }

  &__svg {
    flex-shrink: 0;
    width: 70px;
    height: 70px;
    fill: url("#paint0_linear_2002_2201");

    @include desktop {
      width: 50px;
      height: 50px;
    }

    @include tablet {
      width: 30px;
      height: 30px;
    }

    @include mobile {
      width: 20px;
      height: 20px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 28px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 24px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__img {
    flex-shrink: 0;

    @include desktop {
      width: 270px;
    }

    @include tablet {
      margin-right: 100px;
      width: 194px;
    }

    @include small-tablet {
      margin-right: 50px;
      width: 150px;
    }

    @include mobile {
      display: none;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 19%;
    }
  }
}
