.selection-hero {
  padding: 50px 0 150px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 80px;

    @include mobile {
      flex-direction: column;
      margin-bottom: 20px;
    }
  }

  &__subtext {
    margin: 0;
    width: 445px;
    font-weight: 400;
    font-size: 28px;
    line-height: 130%;
    color: #2d2c3d;

    @include tablet {
      font-size: 22px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      margin-bottom: 10px;
      width: 100%;
      font-size: 15px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 500;
    font-size: 30px;
    line-height: 130%;
    color: #2d2c3d;
  }

  &__inners {
    flex-shrink: 0;
    width: 100%;
    max-width: 500px;

    @include desktop {
      max-width: 400px;
    }

    @include tablet {
      max-width: 350px;
    }

    @include small-tablet {
      max-width: 250px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__video {
    border: 10px solid transparent;
    border-radius: 50px;
    width: 100%;
    height: 550px;
    background: var(--borderGradient);

    @include desktop {
      width: 400px;
      height: 400px;
    }

    @include tablet {
      border: 8px solid transparent;
      border-radius: 35px;
      width: 350px;
      height: 350px;
    }

    @include small-tablet {
      border-radius: 20px;
      width: 250px;
      height: 250px;
    }

    @include mobile {
      width: 100%;
    }

    iframe {
      border-radius: 40px;
      width: 100%;
      height: 100%;

      @include tablet {
        border-radius: 30px;
      }

      @include small-tablet {
        border-radius: 20px;
      }
    }
  }

  &__present-post {
    display: inline-block;
    width: 100%;
    font-weight: 300;
    font-size: 24px;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 13px;
    }
  }

  .hero__btns {
    display: flex;
    justify-content: space-around;
    margin-bottom: 0;
  }
}
