.hero {
  padding: 150px 0 130px;

  @include desktop {
    padding: 70px 0 80px;
  }

  @include tablet {
    padding: 40px 0 60px;
  }

  @include mobile {
    padding: 30px 0 60px;
  }

  &__container {
    position: relative;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  &__content {
    margin-bottom: 80px;
    gap: 10px;

    @include desktop {
      margin-bottom: 60px;
    }

    @include tablet {
      margin-bottom: 40px;
      margin-left: 80px;
    }

    @include small-tablet {
      margin-left: 0;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }

  &__title {
    justify-self: center;
    grid-column: 2 span;
    margin: 0;
    font-weight: 800;
    font-size: 90px;
    line-height: 98%;
    -webkit-text-fill-color: transparent;
    background: linear-gradient(106deg, #da0202 0%, #074bc9 100%);
    -webkit-background-clip: text;
    background-clip: text;

    @include desktop {
      font-size: 70px;
    }

    @include tablet {
      font-size: 50px;
    }

    @include small-tablet {
      font-size: 40px;
    }

    @include mobile {
      font-size: 30px;
      text-align: center;
    }
  }

  &__title--width {
    max-width: 582px;

    @include desktop {
      max-width: 465px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__descr {
    place-self: flex-end center;
    grid-column: 2 span;
    margin: 0;
    max-width: 410px;
    font-weight: 500;
    font-size: 30px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      align-self: center;
      width: 90%;
      font-size: 13px;
      text-align: center;
    }

    br {
      @include mobile {
        display: none;
      }
    }
  }

  &__descr--pic {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: -2px;
      width: 100%;
      height: 5px;
      background-image: url("../img/hero-pic.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__btns {
    // display: flex;
    // justify-content: space-around;
    margin-bottom: 150px;
    gap: 10px;

    @include desktop {
      margin-bottom: 80px;
    }

    @include tablet {
      margin-bottom: 60px;
    }
  }

  &__btns-col {
    justify-self: center;
    grid-column: 2 span;
    max-width: 354px;
  }

  &__btns-text {
    margin: 0;
    margin-bottom: 15px;
    padding-bottom: 10px;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 18px;
    }

    @include mobile {
      display: none;
    }
  }

  &__btns-link {
    width: 100%;
    text-align: center;
  }

  &__present {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;

    @include mobile {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__video {
    flex-shrink: 0;
    margin-left: 60px;
    border: 10px solid transparent;
    border-radius: 50px;
    width: 500px;
    height: 500px;
    background: var(--borderGradient);

    @include desktop {
      width: 400px;
      height: 400px;
    }

    @include tablet {
      border: 8px solid transparent;
      border-radius: 35px;
      width: 350px;
      height: 350px;
    }

    @include small-tablet {
      margin-left: 0;
      border-radius: 20px;
      width: 250px;
      height: 250px;
    }

    @include mobile {
      width: 100%;
    }

    iframe {
      border-radius: 40px;
      width: 100%;
      height: 100%;

      @include tablet {
        border-radius: 30px;
      }

      @include small-tablet {
        border-radius: 12px;
      }
    }
  }

  &__present-content {
    width: 578px;

    @include mobile {
      width: 100%;
    }
  }

  &__present-name {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 60px;
    line-height: 90%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 45px;
    }

    @include tablet {
      margin-bottom: 20px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 30px;
    }

    @include mobile {
      font-size: 25px;
    }
  }

  &__present-name--bold {
    font-weight: 800;
    text-align: end;
  }

  &__present-post {
    display: inline-block;
    margin-bottom: 30px;
    width: 100%;
    font-weight: 300;
    font-size: 24px;
    text-align: center;
    color: #2d2c3d;

    @include tablet {
      margin-bottom: 20px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__present-blockquote {
    margin: 0;
    font-weight: 400;
    font-size: 39px;
    line-height: 130%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include small-tablet {
      font-size: 22px;
    }

    @include mobile {
      font-size: 15px;
    }
  }
}
