.read-also {
  margin-bottom: 80px;

  @include tablet {
    margin-bottom: 70px;
  }

  @include small-tablet {
    margin-bottom: 40px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 48px;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 26px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 22px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
  }

  &__img {
    flex-shrink: 0;

    @include small-tablet {
      width: 120px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }

    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
  }
}
