.info {
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;

    @include small-tablet {
      gap: 40px;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__content--center {
    align-items: center;
  }

  &__content--gap {
    @include mobile {
      gap: 20px;
    }
  }

  &__content--mb {
    margin-bottom: 120px;

    @include desktop {
      margin-bottom: 80px;
    }

    @include tablet {
      margin-bottom: 40px;
    }
  }

  &__img {
    flex-shrink: 0;

    @include desktop {
      width: 615px;
    }

    @include tablet {
      width: 380px;
    }

    @include small-tablet {
      width: 320px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__img--size {
    @include mobile {
      width: 175px;
      height: 200px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 48px;
    line-height: 130%;
    color: var(--5);

    @include desktop {
      font-size: 34px;
    }

    @include tablet {
      font-size: 24px;

      br {
        display: none;
      }
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size:16px;
    }
  }

  &__text--growth {
    font-size: 38px;

    @include desktop {
      font-size: 34px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size:16px;
    }
  }

  &__text--small {
    margin: 0 auto;
    margin-top: 30px;
    max-width: 390px;
    font-weight: 500;
    font-size: 22px;
    line-height: 130%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__text-pic {
    position: relative;
    display: inline-block;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 95%;
      height: 14px;
      background-image: url("../img/info-pic.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}
