.faq {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    gap: 60px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__group-accordion {}

  &__group-img {
    flex-shrink: 0;

    @include mobile {
      justify-items: center;
    }
  }

  &__img {
    position: relative;
    margin-bottom: 60px;
    border: 10px solid transparent;
    border-radius: 50px;
    width: 420px;
    height: 420px;
    background: var(--borderGradient);

    @include tablet {
      margin-bottom: 40px;
      width: 300px;
      height: 300px;
    }

    @include small-tablet {
      margin-bottom: 20px;
      border: 5px solid transparent;
      border-radius: 20px;
      width: 250px;
      height: 250px;
    }

    // @include mobile {
    //   margin-right: auto;
    //   margin-left: auto;
    // }

    img {
      border-radius: 40px;

      @include small-tablet {
        border-radius: 16px;
      }
    }
  }

  &__img-info {
    position: absolute;
    left: 50%;
    bottom: 16px;
    border-radius: 16px;
    padding: 15px;
    width: 287px;
    text-align: center;
    background: rgba(255, 255, 255, 0.85);
    translate: -50%;

    @include tablet {
      padding: 10px;
      width: 205px;
    }

    @include small-tablet {
      padding: 8px;
    }
  }

  &__img-name {
    display: inline-block;
    margin-bottom: 6px;
    font-weight: 700;
    font-size: 18px;
    color: #333;

    @include tablet {
      font-size: 13px;
    }

    @include small-tablet {
      font-size: 11px;
    }
  }

  &__img-post {
    display: inline-block;
    font-weight: 400;
    font-size: 15px;
    color: #2d2c3d;

    @include tablet {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 10px;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 15%;
    }
  }

  .section-title__top {
    @include mobile {
      translate: -30%;
    }
  }

  .hero__btns-link {
    margin-right: auto;
    margin-left: auto;
    width: 100%;

    @include mobile {
      width: 250px;
    }
  }
}

.accordion {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: linear-gradient(190deg, #FFF 0%, #084BC9 100%);
  }

  &:not(:last-child) {
    margin-bottom: 30px;
  }

  &__details {
    margin-bottom: 20px;

    &[open]+.accordion__content {
      grid-template-rows: 1fr;
    }

    &[open] .accordion__title {
      &::after {
        rotate: 135deg;
      }

      &::before {
        rotate: -135deg;
      }
    }
  }

  &__summary {
    list-style: none;

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    padding-right: 50px;
    font-weight: 500;
    font-size: 36px;
    line-height: 110%;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 15px;
    }

    &::after,
    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      border-radius: 10px;
      width: 34px;
      height: 3px;
      background-color: #084BC9;

      @include small-tablet {
        width: 16px;
        height: 2px;
      }
    }

    &::after {
      rotate: 90deg;
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr;
    transition-duration: 0.3s;

    &-body {
      overflow: hidden;
    }
  }

  &__content-body {}

  &__content-text {
    margin: 0;
    padding-bottom: 30px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }
}
