.post-hero {
  margin-bottom: 34px;

  @include mobile {
    margin-bottom: 30px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    gap: 30px;

    @include mobile {
      flex-direction: column;
      margin-bottom: 20px;
      gap: 0;
    }
  }

  &__img {
    flex-shrink: 0;

    img {
      border-radius: 30px;

      @include mobile {
        height: 100%;
      }
    }

    @include tablet {
      width: 468px;
    }

    @include small-tablet {
      width: 312px;
    }

    @include mobile {
      width: 100%;
      height: 290px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: rgba(45, 44, 61, 0.8);

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 11px;
    }
  }
}
