.success-company {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 400;
    font-size: 38px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      margin-bottom: 40px;
      font-size: 24px;
    }

    @include small-tablet {
      margin-bottom: 15px;
      font-size: 20px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px 40px;

    @include tablet {
      gap: 20px;
    }
  }

  &__item {}

  &__img {
    position: relative;
    border: 10px solid transparent;
    border-radius: 50px;
    height: 310px;
    background: var(--borderGradient);

    @include tablet {
      border-radius: 20px;
    }

    img {
      border-radius: 40px;

      @include tablet {
        border-radius: 10px;
        height: 100%;
      }
    }
  }

  &__img-info {
    position: absolute;
    left: 50%;
    bottom: 16px;
    border-radius: 16px;
    padding: 15px;
    width: 287px;
    text-align: center;
    background: rgba(255, 255, 255, 0.85);
    translate: -50%;

    @include tablet {
      padding: 10px;
      width: 205px;
    }

    @include small-tablet {
      padding: 8px;
    }
  }

  &__img-name {
    display: inline-block;
    margin-bottom: 6px;
    font-weight: 700;
    font-size: 18px;
    color: #333;

    @include tablet {
      font-size: 13px;
    }

    @include small-tablet {
      font-size: 11px;
    }
  }

  &__img-post {
    display: inline-block;
    font-weight: 400;
    font-size: 15px;
    color: #2d2c3d;

    @include tablet {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 10px;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 15%;
    }
  }

  .section-title__top {
    @include mobile {
      translate: 0;
    }
  }
}

.success-company-slider {
  overflow: visible;

  .approach-slider__pag {
    display: none;

    @include small-tablet {
      display: block;
      padding-bottom: 40px;
    }
  }

  @include small-tablet {
    overflow: hidden;
    padding-bottom: 35px;

    .swiper-wrapper {
      display: flex;
      column-gap: 0;
    }
  }
}

.selection {
  .success-company .section-title__top {
    translate: 0;
  }

  @include mobile {
    translate: 0;
  }
}
