.business {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  .section-title {
    margin-bottom: 0;
  }

  &__subtitle {
    text-align: center;
  }

  .section-title~.section-subtitle {
    margin-top: 0;
    margin-bottom: -10px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;

    @include small-tablet {
      gap: 10px;
    }

    @include mobile {
      grid-template-columns: 1fr;

    }
  }

  &__cart {
    padding: 10px 35px 30px;
    width: 100%;
    min-height: 525px;
    background-image: url("../img/business-pic.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include desktop {
      padding: 14px 20px 20px;
      min-height: 512px;
    }

    @include tablet {
      min-height: 382px;
    }

    @include small-tablet {
      padding: 12px;
      min-height: 325px;
      background-image: url("../img/business-pic-sm.svg");
    }

    @include mobile {
      justify-self: center;
      width: 255px;
      min-height: 353px;
    }
  }

  &__svg {
    display: block;
    margin: 0 auto;
    margin-bottom: 30px;

    @include tablet {
      width: 70px;
      height: 70px;
    }

    @include small-tablet {
      width: 54px;
      height: 54px;
    }
  }

  &__cart-title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 34px;
    line-height: 110%;
    letter-spacing: -0.01em;
    text-align: center;

    @include desktop {
      margin-bottom: 20px;
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }
  }

  &__list {}

  &__item {
    position: relative;
    padding-left: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include tablet {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 13px;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 10px;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      background-color: #084BC9;

      @include small-tablet {
        top: 6px;
        width: 5px;
        height: 5px;
      }
    }
  }
}

.business-page {
  .section-title {
    margin-bottom: 60px;

    @include tablet {
      margin-bottom: 30px;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  .business__content {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-bottom: 120px;
    gap: 30px;

    @include tablet {
      gap: 20px;
    }

    @include small-tablet {
      grid-template-columns: 1fr 1fr 1fr;
    }

    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  .business__cart {
    min-height: 400px;
    background-image: url("../img/business-pic-page.svg");

    @include tablet {
      min-height: 320px;
    }

    @include small-tablet {
      min-height: 330px;
    }
  }

}
