.dis-scroll .header {
  animation-timeline: none;
}

.header {
  position: sticky;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 23px 0 15px;
  background-color: transparent;
  animation: scrolling-header;
  animation-fill-mode: both;
  animation-range: 100px 200px;
  animation-timeline: scroll();

  @include small-tablet {
    padding: 18px 0;
  }

  @include mobile {
    padding: 14px 0;
  }

  @keyframes scrolling-header {
    to {
      box-shadow: 0 0 10px 0 #084bc9;
      background-color: #fff;
    }
  }

  &__bg {
    position: relative;
    width: 100%;
    max-width: 1920px;
  }


  &__bg-pink {
    position: absolute;
    left: 0;
    top: 0;
    width: 350px;
    height: 960px;
    fill: #FF9494;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    @include tablet {
      gap: 10px;
    }
  }

  &__logo {
    @include tablet {
      width: 90px;
    }
  }

  &__city {
    display: flex;
    align-items: center;
    gap: 4px;

    @include small-tablet {
      display: none;
    }
  }

  &__svg {
    width: 20px;
    height: 21px;
    fill: #fff;
    stroke: #DA0202;
    stroke-width: 1.5px;
  }

  &__city-name {
    font-weight: 500;
    font-size: 15px;
    line-height: 130%;
    color: #333;
  }

  &__social {}

  &__social-list {
    display: flex;
    justify-content: space-around;
    margin-bottom: 5px;
    gap: 10px;

    @include tablet {
      gap: 5px;
    }

    @include mobile {
      margin-bottom: 0;
      gap: 25px;
    }
  }

  &__social-item {}

  &__social-link {}

  &__social-svg {
    width: 28px;
    height: 28px;
    stroke: #fff;
    stroke-width: 0.8px;

    @include mobile {
      width: 22px;
      height: 22px;
    }
  }

  &__social-svg--tg {
    fill: #2FA8DF;
  }

  &__social-svg--wa {
    fill: #269F2E;
  }

  &__social-text {
    font-weight: 400;
    font-size: 14px;
    text-transform: lowercase;
    color: #333;

    @include desktop {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 11px;
    }

    @include mobile {
      display: none;
      margin-bottom: 0;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;

    @include tablet {
      gap: 5px;
    }
  }

  &__contact {
    font-weight: 500;
    font-size: 18px;
    color: #333;

    @include desktop {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 13px;
    }

    span {
      @include mobile {
        display: none;
      }
    }
  }

  &__contact-text {
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    text-transform: lowercase;
    color: #333;

    @include desktop {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 11px;
    }

    @include mobile {
      display: none;
    }
  }

  &__contacts-svg {
    display: none;

    @include mobile {
      display: block;
      width: 19px;
      height: 19px;
      fill: none;
      stroke: #084bc9;
    }
  }

  &__btn {
    display: inline-block;
    border: 1.50px solid #da0202;
    border-radius: 93px;
    padding: 15px 28px;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
    color: #da0202;
    transition: color .3s ease-in-out, background-color .3s ease-in-out;

    @include tablet {
      font-size: 11px;
    }

    @include small-tablet {
      padding: 10px 15px;
      font-size: 10px;
    }

    @include mobile {
      display: none;
    }

    &:hover {
      color: #fff;
      background-color: #da0202;
    }
  }
}

.burger {
  @include burger
}

.acc-menu {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
  display: none;
  overflow: scroll;
  padding: 80px 40px 20px;
  width: 400px;
  height: 100vh;
  background-color: #fff;

  @include mobile {
    padding: 60px 20px 20px;
    width: 100%;
  }

  &.menu--active {
    display: block;
  }

  &__btn {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: #2d2c3d;
  }

  &__btn--none {
    @include mobile {
      display: none;
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
  }
}

.menu-list__item {}


.menu-accordion {
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &__details {
    margin-bottom: 10px;

    &[open]+.menu-accordion__content {
      grid-template-rows: 1fr;
    }

    &[open] .menu-accordion__title {
      &::before {
        rotate: 180deg;
      }
    }
  }

  &__summary {
    list-style: none;

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__title {
    position: relative;
    display: inline-block;
    margin: 0;
    padding-right: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: #2d2c3d;

    &::before {
      content: "";
      position: absolute;
      right: -10px;
      top: 0;
      width: 24px;
      height: 24px;
      background-image: url('../img/chevron-down.svg');
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr;
    transition-duration: 0.3s;

    &-body {
      overflow: hidden;
    }

    &.show {
      display: block;
    }
  }

  &__content-body {}

  &__content-text li{
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: #2d2c3d;
  }
}

.custom-style {
  width: 100%;

  .menu__group-accordion {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .acc-menu__wrap {
    flex-direction: row-reverse;
  }
}

header.scrolled {
  animation: scrolling-header 0.3s forwards; /* Применение обновленной анимации */
}
