.info-mail {
  position: relative;
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-image: url("../img/bg-mail.png");
    background-position: top;
    background-size: 100% 120%;
  }

  &__text {
    margin: 0;
    margin-right: auto;
    margin-bottom: 40px;
    margin-left: auto;
    max-width: 900px;
    font-weight: 400;
    font-size: 38px;
    line-height: 130%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    gap: 130px;

    @include desktop {
      gap: 50px;
    }

    @include small-tablet {
      justify-content: space-between;
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__content-mb {
    margin-bottom: 120px;

    @include desktop {
      margin-bottom: 80px;
    }

    @include tablet {
      margin-bottom: 60px;
    }
  }

  &__img {
    flex-shrink: 0;

    @include desktop {
      width: 400px;
    }

    @include tablet {
      width: 335px;
    }

    @include mobile {
      display: none;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 3%;
    }
  }

  .section-title__top {
    @include mobile {
      translate: 0;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 570px;

  &__label {
    margin-bottom: 30px;

    @include tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__input {
    border-radius: 10px;
    padding: 20px;
    width: 100%;
    height: 84px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    font-size: 20px;
    background: rgba(255, 255, 255, 0.59);

    @include desktop {
      height: 64px;
    }

    @include tablet {
      padding: 16px 20px;
      height: 55px;
      font-size: 18px;
    }

    @include small-tablet {
      height: 50px;
      font-size: 15px;
    }

    @include mobile {
      padding: 12px 20px;
      height: 40px;
      font-size: 13px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 20px;
      color: #949494;

      @include tablet {
        font-size: 18px;
      }

      @include small-tablet {
        font-size: 15px;
      }

      @include mobile {
        font-size: 13px;
      }
    }
  }
}

.custom-checkbox {
  margin-bottom: 30px;
}

.custom-checkbox__field:checked+.custom-checkbox__content::after {
  opacity: 1
}

.custom-checkbox__field:focus+.custom-checkbox__content::before {
  outline: red solid 2px;
  outline-offset: 2px
}

.custom-checkbox__field:disabled+.custom-checkbox__content {
  opacity: .4;
  pointer-events: none
}

.custom-checkbox__text {
  position: relative;
  padding-left: 30px;
  font-weight: 400;
  font-size: 15px;
  line-height: 130%;
  color: #2d2c3d;

  @include mobile {
    font-size: 11px;
  }
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid #949494;
  border-radius: 3px;
  width: 18px;
  height: 18px;
}

.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  background: url("../img/check.svg");
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity .3s
}

.custom-checkbox a {
  color: inherit;
}

.custom-checkbox a:hover {
  color: var(--accent-color)
}

.custom-checkbox a:active {
  color: var(--white-color)
}

.custom-checkbox input {
  appearance: none
}

.custom-checkbox input:focus+.custom-checkbox__text::before {
  outline: 2px dashed var(--white-color);
  outline-offset: 3px
}

.custom-checkbox input:checked+.custom-checkbox__text::after {
  opacity: 1;
}

.custom-checkbox a.focus-visible {
  outline: 2px dashed var(--white-color);
  outline-offset: 3px
}

.selection {
  .info-mail .section-title__top {
    translate: 0;
  }

  .info .section-title__bottom {
    translate: 10%;

    @include mobile {
      translate: 0;
    }
  }

  .info__img {
    @include tablet {
      width: 250px;
    }

    @include mobile {
      width: 242px;
      height: 100%;
    }
  }


  .info-mail .info__content {
    @include mobile {
      position: relative;
    }
  }

  .info-mail .info__text {
    @include mobile {
      padding-right: 50px;
    }
  }

  .info-mail .info__img {
    @include small-tablet {
      width: 170px;
    }

    @include mobile {
      position: absolute;
      right: -50px;
      top: -15%;
      width: 140px;
    }
  }

  .info-mail {
    @include mobile {
      overflow: hidden;
    }
  }
}
