.search {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  & .section-title__top {
    margin-bottom: 0;

    @include mobile {
      translate: 0%;
    }
  }

  // @include mobile {
  //   translate: -14%;
  // }

  .section-title~.section-subtitle {
    margin-top: 0;
  }

  &__list {
    counter-reset: count -1;
    margin-bottom: 120px;

    @include desktop {
      margin-bottom: 60px;
    }

    @include tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__item {
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.59);

    @include desktop {
      padding: 30px;
    }

    @include small-tablet {
      padding: 20px;
    }

    @include mobile {
      padding: 15px;
    }
  }

  &__day {
    counter-increment: count;
    display: inline-block;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 38px;
    line-height: 110%;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 18px;
    }

    @include mobile {
      margin-bottom: 5px;
    }

    &::before {
      content: counter(count);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }
  }
}

.search-page {
  .section-title {
    margin-bottom: 60px;

    @include desktop {
      margin-bottom: 45px;
    }

    @include small-tablet {
      margin-bottom: 10px;
    }
  }

  .search__day {
    &::before {
      content: none;
    }
  }

  .search__list {
    margin-bottom: 0;
  }
}
