.footer {
  position: relative;
  padding: 60px 0;
  background-color: #2d2c3d;

  @include tablet {
    padding: 40px 0;
  }

  @include mobile {
    padding: 20px 0;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/bg-footer.png");
    background-size: cover;
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
    gap: 20px;

    @include mobile {
      flex-direction: column;
      margin-bottom: 30px;
      gap: 15px;
    }
  }

  &__logo {
    @include small-tablet {
      width: 90px;
    }
  }

  &__contact {
    display: flex;
    justify-content: space-between;
    gap: 160px;

    @include mobile {
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__contact-mail {
    font-weight: 400;
    font-size: 18px;
    color: #fff;

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__contact-tel {
    font-weight: 600;
    font-size: 20px;
    text-align: center;
    color: #fff;

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__contact-svg {
    width: 24px;
    height: 24px;
    fill: #fff;
    stroke: #2D2C3D;
    stroke-width: 1.5px;

    @include mobile {
      width: 18px;
    height: 18px;
     }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    gap: 15px;

    @include mobile {
      display: none;
    }
  }

  &__col {
    width: 254px;
  }

  &__col-title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 20px;
    color: #fff;

    @include tablet {
      font-size: 15px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 14px;
    }
  }

  &__col-list {}

  &__col-item {
    &:not(:last-child) {
      margin-bottom: 10px;

      @include small-tablet {
        margin-bottom: 5px;
      }
    }
  }

  &__col-link {
    font-weight: 400;
    font-size: 14px;
    color: #fff;

    @include tablet {
      font-size: 12px;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      gap: 10px;
    }
  }

  &__bottom-copy {
    font-weight: 400;
    font-size: 12px;
    text-transform: uppercase;
    color: #fff;

    @include mobile {
      font-size: 11px;
    }
  }

  &__bottom-policy {
    font-weight: 400;
    font-size: 12px;
    color: #fff;

    @include mobile {
      font-size: 11px;
    }
  }
}
