.reviews-company {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__descr {
    margin: 0;
    margin-right: auto;
    margin-bottom: 60px;
    margin-left: auto;
    max-width: 900px;
    font-weight: 400;
    font-size: 38px;
    line-height: 130%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      margin-bottom: 40px;
      font-size: 22px;
    }

    @include small-tablet {
      margin-bottom: 20px;
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__slider {
    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__card {
    border-radius: 50px;
    padding: 40px 60px;
    background: rgba(255, 255, 255, 0.59);

    @include desktop {
      padding: 30px;
    }

    @include tablet {
      padding: 20px;
    }

    @include tablet {
      border-radius: 20px;
      padding: 15px;
    }

    @include mobile {
      padding: 10px;
    }
  }

  &__img {
    margin-bottom: 50px;
  }

  &__btns {
    padding: 12px 20px;
    width: 100%;
    text-align: center;

    @include desktop {
      padding: 10px 15px;
      font-size: 15px;
    }

    @include tablet {
      font-size: 13px;
    }

    @include mobile {
      font-size: 11px;
    }
  }

  &__block {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      gap: 30px;
    }

  }

  &__block-text {
    width: 100%;
    max-width: 900px;
    font-weight: 400;
    font-size: 46px;
    line-height: 130%;
    color:  #2d2c3d;

    @include desktop {
      font-size: 36px;
    }

    @include tablet {
      max-width: 100%;
      font-size: 28px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 5%;
    }
  }
}

.selection {
  .reviews-company .section-title__top {
    translate: -10%;

    @include tablet {
      translate: 0;
    }

    .reviews-company .section-title__bottom {
      @include mobile {
        translate: 0;
      }
    }
  }
}

.reviews-company-slider {
  padding-bottom: 30px;
}
