.services {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 400;
    font-size: 38px;
    line-height: 90%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 30px;
    }

    @include tablet {
      margin-bottom: 40px;
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      margin-bottom: 15px;
      font-size: 15px;
    }
  }

  &__info-mb {
    margin-bottom: 120px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;

    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  &__card {
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 30px;
    padding: 60px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.59);
    gap: 30px;

    @include desktop {
      padding: 40px;
    }

    @include tablet {
      padding: 20px;
    }

    @include mobile {
      flex-direction: column;
      padding: 10px;
      gap: 20px;
    }

    &:first-child {
      flex-direction: column;
    }

    &:nth-child(2) {
      flex-direction: column-reverse;
    }

    &:nth-child(3) {
      flex-direction: row-reverse;
      grid-column: 2 span;

      @include mobile {
        grid-column: 1 span;
      }
    }

    &:last-child {
      flex-direction: row-reverse;
      grid-column: 2 span;
      gap: 50px;

      @include mobile {
        grid-column: 1 span;
        gap: 20px;
      }

      &.services__card--row {
        flex-direction: row;
      }
    }
  }

  &__card-title {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 48px;
    line-height: 90%;
    text-align: center;
    color: #074bc9;

    @include desktop {
      font-size: 38px;
    }

    @include tablet {
      font-size: 28px;
    }

    @include small-tablet {
      margin-bottom: 20px;
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__img {
    flex-shrink: 0;

    @include mobile {
      display: none;
    }
  }

  &__pic-svg {
    position: absolute;
    right: 0;
    top: 0;

    @include desktop {
      width: 100px;
    }

    @include tablet {
      width: 70px;
    }

    @include small-tablet {
      width: 50px;
    }

    @include mobile {
      display: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 38px;
    line-height: 110%;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 18px;
    }

    @include mobile {
      margin-bottom: 5px;
      font-size: 15px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 9%;
    }
  }

  .section-title__top {
    @include mobile {
      translate: -20%;
    }
  }
}

.selection .services__img {
  align-self: flex-end;
  width: 240px;
  height: 250px;
}
