.news {
  padding-bottom: 120px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 50px;
  }

  @include small-tablet {
    margin-bottom: 40px;
  }

  &__top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    gap: 20px;

    @include desktop {
      margin-bottom: 80px;
    }

    @include tablet {
      margin-bottom: 50px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__top-fresh {
    position: absolute;
    right: 0;
    top: -12px;
    display: inline-block;
    border: 3px solid transparent;
    border-radius: 55px;
    padding: 10px;
    background: var(--borderGradient);

    @include mobile {
      right: 20px;
      top: -20px;
      padding: 10px 15px;
    }
  }

  &__top-fresh-text {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 13px;
    }
  }

  &__content {}

  &__img {
    position: relative;
    flex-shrink: 0;

    img {
      border-radius: 30px;
      height: 350px;

      @include tablet {
        border-radius: 20px;
        height: 260px;
      }

      @include small-tablet {
        height: 229px;
      }
    }
  }

  &__img--mb {
    margin-bottom: 20px;

    @include small-tablet {
      margin-bottom: 10px;
    }
  }

  &__img--height {
    height: 400px;

    img {
      border-radius: 30px;
      height: 100%;
    }

    @include desktop {
      width: 500px;
      height: 350px;
    }

    @include small-tablet {
      width: 350px;
      height: 290px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__date {
    display: inline-block;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: #2d2c3d;

    @include mobile {
      font-size: 15px;
    }
  }

  &__date--tr {
    width: 100%;
    text-align: right;
  }

  &__date--mb {
    margin-bottom: 10px;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 25px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #2d2c3d;

    @include tablet {
      font-size: 22px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__descr {
    display: -webkit-box;
    overflow: hidden;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: #2d2c3d;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__descr--line {
    -webkit-line-clamp: 4;
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: #084bc9;

    @include small-tablet {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 14px;
    }

    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px 30px;

    @include desktop {
      gap: 40px 20px;
    }

    @include small-tablet {
      gap: 30px 20px;
    }

    @include mobile {
      grid-template-columns: 1fr;
      gap: 15px;
    }
  }

  &__item {
    position: relative;
  }
}
