*,
*::before,
*::after {
  box-sizing: border-box;
}

// * {
//   outline: 1px solid red;
// }

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-image: url("../img/bg.png");
    background-size: cover;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

// .site-container {
//   overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
// }

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.site-container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title-gradient {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(106deg, #da0202 0%, #074bc9 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

.text-color {
  color: #084bc9;
}

.text-dots {
  text-decoration: underline dotted;
  color: #DA0202;
}

.text-border {
  border: 1px solid #da0202;
  border-radius: 100px;
  padding: 2px 8px;
  line-height: 1;

  // background: var(--borderGradient);

  @include mobile {
    padding: 1px 6px;
  }
}

.text-bold {
  font-weight: 600;
}

.btn-color {
  display: inline-block;
  border: 2px solid transparent;
  border-radius: 93px;
  padding: 20px 30px;
  font-weight: 500;
  font-size: 20px;
  line-height: 98%;
  text-align: center;
  color: #fff;
  background-color: #da0202;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;

  &:hover {
    border: 2px solid #da0202;
    color: #da0202;
    background-color: #fff;
  }


  @include tablet {
    font-size: 18px;
  }

  @include small-tablet {
    font-size: 15px;
  }

  @include mobile {
    padding: 12px 30px;
    font-size: 11px;
  }
}

.btn-color-width {
  width: 350px;
}

.btn-color--dashed {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: -15px;
    border-top: 2px dashed #da0202;
    width: 100%;
    translate: -50%;

    @include mobile {
      display: none;
    }
  }
}

.btn-transparent {
  display: inline-block;
  border: 2px solid #084bc9;
  border-radius: 93px;
  padding: 20px 30px;
  font-weight: 500;
  font-size: 20px;
  line-height: 98%;
  color: #084bc9;
  background: #fff;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;

  &:hover {
    border: 2px solid transparent;
    color: #fff;
    background-color: #084bc9;

  }

  @include tablet {
    font-size: 18px;
  }

  @include small-tablet {
    font-size: 15px;
  }

  @include mobile {
    padding: 12px 30px;
    font-size: 11px;
  }
}

.btn-transparent--dashed {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: -15px;
    border-top: 2px dashed #084bc9;
    width: 100%;
    translate: -50%;

    @include mobile {
      display: none;
    }
  }
}

.section-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  margin-bottom: 60px;
  font-size: 70px;
  line-height: 90%;
  color: #2d2c3d;

  @include desktop {
    margin-bottom: 45px;
    font-size: 45px;
  }

  @include small-tablet {
    margin-bottom: 20px;
    font-size: 35px;
  }

  @include mobile {
    font-size: 25px;
  }

  &__top {
    font-weight: 400;
    translate: -35%;
  }

  &__bottom {
    font-weight: 800;
    translate: 35%;
  }

  &__bottom--translate {
    translate: 0;
  }

  ~.section-subtitle {
    margin-top: 60px;
    margin-bottom: 0;

    @include tablet {
      margin-top: 0;
    }

    @include small-tablet {
      margin-top: -25px;
    }
  }
}

.section-title--small {
  font-size: 38px;

  @include tablet {
    font-size: 35px;
  }

  @include small-tablet {
    margin-bottom: 20px;
  }

  @include mobile {
    font-size: 25px;
  }
}

.section-subtitle {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 133px;
  line-height: 0.60;
  color: rgba(45, 44, 61, 0.05);

  @include desktop {
    margin-bottom: 40px;
    font-size: 99px;
  }

  @include tablet {
    font-size: 80px;
  }

  @include small-tablet {
    margin-bottom: 20px;
    font-size: 60px;
  }

  @include mobile {
    font-size: 36px;
  }

  &__top {
    text-align: left;
  }

  &__bottom {
    text-align: right;
  }
}

.section-subtitle-approach {
  position: absolute;
  left: 0;
  bottom: -55px;
  margin: 0;
  font-weight: 600;
  font-size: 230px;
  line-height: 2.2;
  text-align: center;
  color: rgba(45, 44, 61, 0.06);

  @include desktop {
    bottom: -90px;
    font-size: 185px;
  }

  @include tablet {
    bottom: -20px;
    font-size: 150px;
    line-height: 2.4;
  }

  @include small-tablet {
    bottom: 0;
    font-size: 110px;
    line-height: 2.4;
  }

  @include mobile {
    display: none;
  }
}

.page-title {
  display: inline-block;
  margin: 0;
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 70px;
  line-height: 98%;
  -webkit-text-fill-color: transparent;
  color: #2d2c3d;
  background: linear-gradient(106deg, #da0202 0%, #074bc9 100%);
  -webkit-background-clip: text;
  background-clip: text;

  @include desktop {
    font-size: 60px;
  }

  @include tablet {
    font-size: 50px;
  }

  @include small-tablet {
    font-size: 40px;
  }

  @include mobile {
    margin-bottom: 20px;
    font-size: 30px;
  }
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 30px;
}

.video {
  position: relative;

  &:hover {
    cursor: url('../img/cursore.png'), auto;
  }

  img {
    display: block;
    border-radius: 50px;
    width: 100%;
    height: 100%;
  }
}

.video__play {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 120px;
  height: 120px;
  translate: -50% -50%;

  @include small-tablet {
    width: 100px;
    height: 100px;
  }

  @include mobile {
    width: 70px;
    height: 70px;
  }

  &:hover {
    cursor: url('../img/cursore.png'), auto;
    scale: 1.2;
  }
}

.video .html5-video-player {
  cursor: url('../img/cursore.png'), auto;
}

.video__play--size {
  width: 50px;
  height: 50px;
}


.clearfix::after {
  content: "";
  display: table;
  clear: both;
}
