.faq-tags {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 50px;
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 600;
    font-size: 48px;
    line-height: 90%;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      margin-bottom: 10px;
      font-size: 20px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 30px;

    @include desktop {
      gap: 20px;
    }

    @include tablet {
      gap: 10px;
    }

    @include mobile {
      flex-wrap: nowrap;
    }
  }

  &__item {
    border-radius: 50px;
    padding: 15px 20px;
    background: rgba(255, 255, 255, 0.59);
    transition: background-image .3s ease-in-out;

    &:hover {
      background-image: linear-gradient(30deg, #cc6b7f 0%, #93b9ff 100%);
    }

    @include small-tablet {
      padding: 10px;
    }

    @include mobile {
      display: none;
    }

    &:nth-child(-n+2) {
      @include mobile {
        display: flex;
      }
    }

  }

  &__link {
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 13px;
    }
  }
}
