.number {
  padding-bottom: 120px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 120px;
    gap: 30px;

    @include desktop {
      margin-bottom: 80px;
      gap: 20px;
    }

    @include tablet {
      margin-bottom: 60px;
    }

    @include mobile {
      grid-template-columns: 1fr;
      gap: 10px;
    }
  }

  &__item {
    border-radius: 30px;
    padding: 20px 40px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.59);

    @include small-tablet {
      padding: 20px;
    }
  }

  &__name {
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 38px;
    line-height: 110%;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      margin-bottom: 10px;
      font-size: 20px;
    }

    @include mobile {
      margin-bottom: 5px;
      font-size: 18px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }
  }

  .section-title {
    @include small-tablet {
      margin-bottom: 10px;
    }
  }

  .info__content {
    @include mobile {
      position: relative;
      flex-direction: row;
    }
  }

  .info__text {
    @include mobile {
      padding-right: 50px;
    }
  }

  .info__img {
    @include small-tablet {
      width: 170px;
    }

    @include mobile {
      position: absolute;
      right: -72px;
      top: -15%;
      width: 140px;
    }
  }

    @include mobile {
      overflow: hidden;
    }
}
