.reviews-client {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__descr {
    margin: 0;
    padding: 0 30px 30px;
    font-weight: 400;
    font-size: 36px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      margin-bottom: 10px;
      font-size: 15px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 225px 225px 1fr 1fr 1fr;
    grid-template-rows: 260px 260px;
    gap: 30px;

    @include desktop {
      gap: 20px;
    }

    @include tablet {
      // grid-auto-flow: column;
      grid-template-columns: 186px 186px 1fr 1fr;
      grid-template-rows: 224px 224px;
    }

    @include small-tablet {
      grid-template-columns: 152px 152px 1fr 1fr;
      grid-template-rows: 160px 160px;
      gap: 10px;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
  }

  &__card {
    position: relative;
    border: 10px solid transparent;
    border-radius: 50px;
    background: var(--borderGradient);

    @include tablet {
      border-radius: 20px;
    }

    &:first-child {
      grid-column: 1 / 3;
      grid-row: 1 / 3;
      height: 490px;


      @include small-tablet {
        height: 100%;
      }

      @include mobile {
        margin-bottom: 50px;
        height: 300px;
      }

      .reviews-client__name {
        padding: 17px 27px;
        font-weight: 600;
        font-size: 20px;
      }
    }

    &:nth-child(n+2) {
      @include mobile {
        height: 300px;
      }
    }

    &:nth-last-child(-n+2) {
      @include tablet {
        display: none;
      }
    }
  }

  &__video {
    height: 100%;

    iframe {
      border-radius: 40px;
      width: 100%;
      height: 100%;

      @include tablet {
        border-radius: 12px;
      }
    }
  }

  &__name {
    position: absolute;
    left: 10px;
    top: 10px;
    margin: 10px 15px;
    border-radius: 30px;
    padding: 5px;
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;
    color: #2d2c3d;
    background: rgba(255, 255, 255, 0.59);

    @include mobile {
      font-size: 10px;
    }
  }

  &__prew {
    position: absolute;
    left: 50%;
    bottom: -90px;
    border-radius: 24px;
    padding: 30px;
    width: 100%;
    max-width: 396px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    font-weight: 600;
    font-size: 28px;
    line-height: 110%;
    text-align: center;
    background-color: #fff;
    translate: -50%;

    @include tablet {
      bottom: -40px;
      padding: 20px;
      max-width: 350px;

      @include small-tablet {
        padding: 10px;
      }
    }
  }

  &__prew-text {
    font-weight: 600;
    font-size: 28px;
    line-height: 110%;
    text-align: center;

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 17px;
    }
  }
}
