// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --second-family: "Inter", sans-serif;
  --third-family: "RF Dewi Extended", sans-serif;
  --content-width: 1370px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --gradient: linear-gradient(106deg, rgba(218, 2, 2, 0.5) 0%, rgba(7, 75, 201, 0.5) 100%);
  --borderGradient:  linear-gradient(var(--light-color), var(--light-color)) padding-box, var(--gradient) border-box;



}
