.case {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__card {
    display: flex;
    flex-direction: column;
    border-radius: 50px;
    padding: 40px 60px;
    width: 516px;
    min-height: 459px;
    box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.06);
    background: rgba(255, 255, 255, 0.59);

    @include desktop {
      padding: 20px 35px;
      width: 100%;
      min-height: 245px;
    }

    @include tablet {
      border-radius: 30px;
      padding: 18px 24px;
    }

    @include small-tablet {
      padding: 15px 25px;
      min-height: 196px;
    }
  }

  &__card-name {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 38px;
    line-height: 120%;
    color: #2d2c3d;

    @include desktop {
      font-size: 22px;
    }

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 17px;
    }
  }

  &__card-where {
    font-weight: 300;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      margin-bottom: 8px;
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__block {
    margin-top: auto;
  }

  &__total {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 8px;
  }

  &__text-reg {
    display: inline-block;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__text-bold {
    display: inline-block;
    margin-right: 14px;
    margin-left: auto;
    font-weight: 800;
    font-size: 40px;
    line-height: 98%;
    color: #2d2c3d;

    @include desktop {
      font-size: 23px;
    }

    @include small-tablet {
      font-size: 17px;
    }
  }

  &__text-medium {
    display: inline-block;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    width: 100%;
    gap: 30px;

    @include desktop {
      margin-top: 20px;
      gap: 10px;
    }
  }

  &__btns-link {
    padding: 12px 24px;
    text-align: center;

    @include desktop {
      padding: 8px 18px;
      font-size: 13px;
    }

    @include small-tablet {
      padding: 6px 14px;
      font-size: 10px;
    }

    @include mobile {
      padding: 5px 12px;
      font-size: 10px;
    }
  }

  .section-title__top {
    @include mobile {
      translate: -10%;
    }
  }

  .section-title__bottom {
    @include mobile {
      translate: 10%;
    }
  }
}

.inner {
  display: flex;
  padding-left: max(15px, calc((100% - 1390px) / 2));

  @include desktop {
    padding-left: max(15px, calc((100% - 1170px) / 2));
  }

  @include tablet {
    padding-left: max(15px, calc((100% - 9600px) / 2));
  }

  @include small-tablet {
    padding-left: max(15px, calc((100% - 730px) / 2));
  }
}

.case-slider {
  padding-bottom: 50px;
  width: calc(100% - 30px);

  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;

  }
}
