.post-article {
  grid-column: 7 span;

  @include tablet {
    grid-column: 8 span;
  }

  .section-title__top {
    @include mobile {
      translate: 0;
    }
  }
}

.post-work__video {
  margin-bottom: 70px;
  border: 10px solid transparent;
  border-radius: 50px;
  width: 100%;
  height: 500px;
  background: var(--borderGradient);

  @include tablet {
    margin-bottom: 60px;
  border: 8px solid transparent;

  }

  @include small-tablet {
    margin-right: auto;
    margin-left: auto;
    border-radius: 20px;
    width: 400px;
    height: 400px;
  }

  @include mobile {
    margin-bottom: 40px;
    width: 100%;
    height: 280px;
  }

  iframe {
    border-radius: 40px;
    width: 100%;
    height: 100%;

    @include tablet {
      border-radius: 30px;
    }

    @include small-tablet {
      border-radius: 20px;
    }
  }
}

.post {
  &__inner {
    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(12, 1fr);

    @include  small-tablet {
      display: flex;
      flex-direction: column-reverse;
    }
  }
}

.post-content {
  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 28px;
    line-height: 140%;
    color: #2d2c3d;

    @include tablet {
      font-size: 26px;
    }

    @include mobile {
      font-size: 22px;
    }
  }

  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  li {
    position: relative;
    list-style: none;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &::before {
      content: "";
      position: absolute;
      left: -15px;
      top: 10px;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      background-color: #084BC9;

      @include small-tablet {
        top: 6px;
      }
    }
  }
}

.post-aside {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  grid-column: 9/13;
  padding-left: 20px;

  @include small-tablet {
    justify-content: flex-start;
  }
}


.post-info {
  position: sticky;
  top: 100px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  min-height: 330px;
  max-width: 430px;

  @include small-tablet {
    position: relative;
    top: 0;
    margin-bottom: 40px;
    min-height: auto;
    max-width: 100%;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 38px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #2d2c3d;

    @include desktop {
      font-size: 28px;
    }

    @include tablet {
      font-size: 26px;
    }

    @include small-tablet {
      margin-bottom: 15px;
    }

    @include mobile {
      font-size: 24px;
    }
  }

  &__list {
    @include small-tablet {
      padding-left: 20px;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &::before {
      content: "";
      position: absolute;
      left: -15px;
      top: 10px;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      background-color: #084BC9;

      @include mobile {
        top: 5px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    color: rgba(45, 44, 61, 0.7);

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }
}

.post-gallery {
  margin-bottom: 70px;

  @include small-tablet {
    margin-bottom: 40px;
  }

  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 30px;
    line-height: 120%;
    letter-spacing: -0.02em;
    text-align: center;
    color: #2d2c3d;

    @include tablet {
      font-size: 28px;
    }

    @include mobile {
      font-size: 22px;
    }
  }

  &__figure {
    margin: 0;


    img {
      border-radius: 50px;
      height: 100%;
      height: 630px;

    @include small-tablet {
      height: 480px;
    }

    @include mobile {
      height: 300px;
    }
    }
  }

  &__figcaption {
    margin-top: 25px;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    text-align: center;
    color: rgba(45, 44, 61, 0.8);
  }

  &__btns {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    margin-left: auto;
    width: 130px;

    @include mobile {
      margin-bottom: 17px;
    }
  }

  &__btn {
    z-index: 11;

    svg {
      width: 14px;
      height: 27px;
      fill: transparent;
      stroke: #084bc9;
      stroke-width: 1.25px;

      @include tablet {
        width: 12px;
        height: 24px;
      }
    }
  }

  &__prev {
    &.swiper-button-disabled svg {
      stroke: #2d2c3d;
    }
  }

  &__next {
    &.swiper-button-disabled svg {
      stroke: #2d2c3d;
    }
  }

  &__pag {
    left: 50%;
    top: 40%;
    bottom: inherit;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: -0.02em;
    color: rgba(45, 44, 61, 0.6);
    translate: -50% -50%;

    @include tablet{
      font-size: 16px;
    }

    .swiper-pagination-current {
      padding-right: 10px;
    }

    .swiper-pagination-total {
      padding-left: 10px;
    }
  }
}

.post-att {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 70px;
  border-radius: 50px;
  padding: 40px;
  background: #fff;

  @include small-tablet {
    margin-bottom: 40px;
  }

  @include mobile {
    padding: 15px;
  }

  &__svg {
    flex-shrink: 0;
    margin-right: 30px;
    width: 48px;
    height: 48px;
    fill: url("#paint0_linear_892_6793");

  }

  &__text {
    display: inline-block;
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }
  }
}

.post-blockquote {
  position: relative;
  display: inline-block;
  margin: 10px;
  margin-top: 70px;
  margin-bottom: 70px;
  border: 6px solid transparent;
  border-radius: 10px;
  padding: 15px 70px;
  background: var(--borderGradient);
  transition: all .3s ease;

  @include tablet {
    margin-top: 60px;
    margin-bottom: 60px;
    padding: 20px 60px;
  }

  @include small-tablet {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @include mobile {
    padding: 10px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    display: block;
    background-color: #fff;
    transform: translate(-50%, -50%);
    transform-origin: 50% 50%;
  }

  &::before {
    width: calc(100% + 14px);
    height: 80%;
    transition: height 0.6s;
  }

  &::after {
    width: 80%;
    height: calc(100% + 14px);
    transition: width 0.6s;
  }

  &__text {
    position: relative;
    z-index: 10;
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    text-align: center;
    color: #2d2c3d;

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__autor {
    position: relative;
    z-index: 10;
    margin: 0;
    font-weight: 400;
    font-size: 17px;
    line-height: 140%;
    text-align: center;
    color: #2d2c3d;
  }
}

.post-table {
  margin-bottom: 30px;

  &__thead {
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: #084bc9;

    @include tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__thead-tr {
    margin-right: 20px;
  }

  &__thead-th {
    padding: 20px;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: #084bc9;

    @include small-tablet {
      font-size: 15px;
    }
  }

  &__tbody {}

  &__tbody-tr {}

  &__tbody-td {
    padding: 18px 0 20px;
    font-weight: 400;
    font-size: 17px;
    line-height: 140%;
    color: #2d2c3d;

    @include small-tablet {
      font-size: 15px;
    }

    &:not(:first-child) {
      text-align: center;
    }
  }
}

.post-conclusion {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 50px;
  padding: 40px;
  background: #fff;

  @include mobile {
    padding: 20px;
  }

  &__title {
    display: inline-block;
    margin: 0 auto;
    margin-bottom: 28px;
    font-weight: 800;
    font-size: 36px;
    line-height: 110%;
    text-align: center;
    -webkit-text-fill-color: transparent;
    background: linear-gradient(106deg, #da0202 0%, #074bc9 100%);
    -webkit-background-clip: text;
    background-clip: text;

    @include small-tablet {
      margin-bottom: 15px;
      font-size: 32px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: #2d2c3d;

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
    }
  }
}
